*, *::after, *::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.pb-container {
  margin: 0px auto;
  border: 1px solid;
  padding: 0px;
}

.pb-header {
  padding: 10px;
  text-align: center;
  background-color: #ababab;
}
.pb-header h1 {
  margin: 0;
}

.pb-footer {
  text-align: right;
  padding: 10px;
  border-top: 1px solid;
}

.pb-footer button {
  min-width: 100px;
  height: 30px;
}


.pb-list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.pb-list li {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid;
}
.pb-list li:last-child {
  border-bottom: 0;
}

.pb-list li span {
  flex-grow: 0;
  flex-basis: 100%;
  padding: 10px 10px;
}
.pb-list li.pg-list-head {
  font-weight: bold;
  font-size: 1.2em;
  background-color: #dedede;
}
.pb-list li span {
  order-right: 1px solid;
}
.pb-list li span:last-child {
  border-right: 0;
  flex: 10%;
  font-weight: bold;
  font-size: 1.5em;
  cursor: pointer;
  text-align: center;
}

.pb-modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .3);
  z-index: 1000;
}

.pb-modal {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  width: 450px;
  background-color: #fff;
}

.pb-modal > div {
  position: relative;
  padding: 15px 20px;
}
.pb-modal-header {
  border-bottom: 1px solid;
  font-size: 1.5em;
}
.pb-modal-content {
}
.pb-modal-footer {
  border-top: 1px solid;
  text-align: right;
}
.pb-modal-footer button {
  min-width: 80px;
  height: 30px;
  margin-left: 5px;
}
.pb-modal-field {
  display: flex;
  flex-direction: column;
  margin: 15px 0;
}
.pb-modal-field input {
  height: 35px;
  margin-top: 3px;
  padding: 0 5px;
}



